import React, { Component } from 'react'
import {Container,Paper} from '@material-ui/core';
import TabBox from './TabBox';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

class App extends Component {
    constructor(props){
        super(props);
        this.state = ({
            todoData:[],
            confirmDialogOpen: false,
            confirmUpdateOpen: false,
            TaskID:'',
            task:'',
            helpDialog:true,
            check:false,
        })
    }

    componentWillMount (){
        let isBackup = JSON.parse(localStorage.getItem('todoMaticData'));
        if(isBackup && isBackup.length) {
            this.setState({todoData:isBackup})
        }
        let check=false;
        (function(a){if(/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(a)||/1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0,4))) check = true;})(navigator.userAgent||navigator.vendor||window.opera); 
        this.setState({check})
    }

    componentDidMount(){
        document.getElementById('todoText').focus();
    }

    addToDo(e){
        e.preventDefault();
        let text = document.getElementById('todoText').value;
        if (text !== "") {
            let todoArr = {
                task:text,
                status:'active',
                id:Date.now()
            }
            this.state.todoData.push(todoArr);
            let temp = this.state.todoData;
            this.setState({todoData: temp})
            document.getElementById('todoText').value = "";
            localStorage.setItem('todoMaticData', JSON.stringify(temp));
        }
    }

    deleteTask =(id,e) => {
        this.setState({
            confirmDialogOpen:true,
            TaskID:id
        })
    }

    delete = () =>{
        const filteredItems= this.state.todoData.filter((item,i)=> item.id!==this.state.TaskID);
        this.setState({
            todoData:filteredItems,
            confirmDialogOpen:false
        })
        localStorage.setItem('todoMaticData', JSON.stringify(filteredItems));
    }

    editTask=(id,task,e)=>{
        this.setState({
            confirmUpdateOpen:true,
            TaskID:id,
            task
        }) 
    }

    update = ()=>{
        const items = this.state.todoData;
        items.map((item, i) => {
            if (item.id === this.state.TaskID) {
                item.task = this.state.task;
            }
            return null;
        })
        this.setState({
            todoData: items,
            confirmUpdateOpen:false
        })
        localStorage.setItem('todoMaticData', JSON.stringify(items));
    }

    handleComplete(isChecked,index) {
        const items = this.state.todoData;
        items.map((item, i) => {
            if (i === index) {
                (isChecked) ? item.status = "complete" : item.status = "active" ;
            }
            return null;
        })
        this.setState({
            todoData: items,
        })
        localStorage.setItem('todoMaticData', JSON.stringify(items));
    }

    render() {
        return (
            <Container maxWidth="lg">
                <div className="help" onClick={()=>{this.setState({helpDialog:true})}}><br/><br/><br/><br/></div>
                <h1 className={(this.state.check)?"text-danger logo display-4 text-center":"text-danger logo display-1 text-center"}>ToDo Matic</h1>
                <Paper className="p-4 mx-auto">
                    <div className="row">
                        <form className="input-group mb-3 form-group col-sm-12" onClick={this.addToDo.bind(this)}> 
                            <input type="text" className="form-control btn-lg" id="todoText" placeholder="What needs to be done?" aria-label="What needs to be done?" aria-describedby="basic-addon2" />
                            <div className="input-group-append w-50">
                                <button className="btn btn-outline-secondary bg-info text-light" type="submit">Add</button>
                            </div>
                        </form>
                    </div>
                <TabBox scrollButtons="desktop" check={this.state.check} variant="scrollable" todoData={this.state.todoData} deleteTask={(e,id)=>this.deleteTask.bind(e,id)} editTask={(e,id,task)=>this.editTask.bind(e,id,task)} handleComplete={(this.handleComplete.bind(this))} />
                </Paper>
                <Dialog
                    open={this.state.confirmDialogOpen}
                    TransitionComponent={Transition}
                    keepMounted
                    maxWidth="sm"
                    fullWidth={true}
                    onClose={()=>this.setState({confirmDialogOpen:false})}
                    aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogTitle className="text-dark" id="alert-dialog-slide-title">{"Delete Alert!!"}</DialogTitle>
                    <DialogContent dividers>
                        <p className="display-4" id="alert-dialog-slide-description">
                            Are you sure ?
                        </p>
                    </DialogContent>
                    <DialogActions>
                        <Button className="btn btn-outline-secondary bg-secondary text-light" onClick={()=>this.setState({confirmDialogOpen:false})} color="primary">
                            No
                        </Button>
                        <Button className="btn btn-outline-secondary bg-danger text-light"  onClick={()=>this.delete()} color="secondary">
                            YES
                        </Button>
                    </DialogActions>
                </Dialog>
                <Dialog
                    open={this.state.confirmUpdateOpen}
                    TransitionComponent={Transition}
                    keepMounted
                    maxWidth="sm"
                    fullWidth={true}
                    onClose={()=>this.setState({confirmUpdateOpen:false})}
                    aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogTitle className="text-dark" id="alert-dialog-slide-title">{"Edit Task"}</DialogTitle>
                    <DialogContent dividers>
                        <div className="row">
                            <div class={(this.state.check)?"col-12":"col-md-10"}>
                                <form noValidate autoComplete="off">
                                    <TextField id="editedTask" label="Task" variant="outlined" value={this.state.task} onChange={(e)=>this.setState({task:e.target.value})} fullWidth={true} />
                                </form>
                            </div>
                            {(!this.state.check)?
                            <div class="col-md-2 bot editBot "><br/><br/>
                            </div>:null}
                        </div>
                    </DialogContent>
                    <DialogActions>
                        <Button className="btn btn-outline-secondary bg-secondary text-light" onClick={()=>this.setState({confirmUpdateOpen:false})} color="primary">
                            Cancel
                        </Button>
                        <Button className="btn btn-outline-secondary bg-danger text-light"  onClick={()=>this.update()} color="secondary">
                            Update
                        </Button>
                    </DialogActions>
                </Dialog>
                <Dialog
                    open={this.state.confirmDialogOpen}
                    TransitionComponent={Transition}
                    keepMounted
                    maxWidth="sm"
                    fullWidth={true}
                    onClose={()=>this.setState({confirmDialogOpen:false})}
                    aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogTitle className="text-dark" id="alert-dialog-slide-title">{"Delete Alert!!"}</DialogTitle>
                    <DialogContent dividers>
                        <p className="display-4" id="alert-dialog-slide-description">
                            Are you sure ?
                        </p>
                    </DialogContent>
                    <DialogActions>
                        <Button className="btn btn-outline-secondary bg-secondary text-light" onClick={()=>this.setState({confirmDialogOpen:false})} color="primary">
                            No
                        </Button>
                        <Button className="btn btn-outline-secondary bg-danger text-light"  onClick={()=>this.delete()} color="secondary">
                            YES
                        </Button>
                    </DialogActions>
                </Dialog>
                <Dialog
                    open={this.state.helpDialog}
                    TransitionComponent={Transition}
                    keepMounted
                    maxWidth="sm"
                    fullWidth={true}
                    onClose={()=>this.setState({helpDialog:false})}
                    aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogContent dividers className="bg-img">
                        <div className="row relative">
                            <div class="">
                                <span className="logo display-4 text-danger col-15">ToDoMatic</span>
                            </div>
                            <div class="right" onClick={()=>this.setState({helpDialog:false})}>
                                <IconButton aria-label="close" className="text-danger" >
                                    <b>X</b>
                                </IconButton>
                            </div>
                        </div>
                        <div className="row">
                            <div class="col-md-7 col-sm-12">
                                <ul className="text-dark text-lg">
                                <li>Create your daily life ToDo.</li>
                                <li>Add all ToDo, It will display in Table.</li>
                                <li>Once your any task is completed from ToDo, Check that task row from Table.</li>
                                <li>Work until you not completed all the Task.</li>
                                <li>Note: Here 3 tabs are available to check your work progress (All / Active / Completed)</li>
                            </ul>
                            </div>
                            <div class="col-md-5 col-sm-12 bot "><br/><br/><br/><br/><br/><br/><br/>
                            </div>
                        </div>
                    </DialogContent>
                </Dialog>
            </Container>
        )
    }
}

export default App;